import uniIcons from "../../components/uni-icons/uni-icons.vue"
import commongood from '../../components/common/commongood.vue'
import {
	mapState,
	mapActions
} from 'vuex'
export default {
	name: "",
	components: {
		uniIcons,
		commongood
	},
	props: {},
	data() {
		return {
			// 接收传过来的type
			type: '',
			// 升序还是降序
			order: '',
			// 控制导航栏变色的参数
			navNum: 0,
			// 控制升序降序的参数
			sort: 0
		}
	},
	methods: {
		...mapActions(['getNewRec']),
		// 更改排序方式
		changeRank(num) {
			this.navNum = num
			this.sort = 0
			this.order = ''
			if (this.navNum === 1) {
				this.order = 'asc'
			}
			this.getNewRec({
				type: this.type,
				order: this.order
			})
		},
		// 更改升序降序
		upSort(sortNum) {
			this.sort = sortNum
			if (this.sort === 0) {
				this.order = 'asc'
			} else {
				this.order = 'desc'
			}
			this.getNewRec({
				type: this.type,
				order: this.order
			})
		}
	},
	mounted() {
		if (this.type !== '') {
			this.getNewRec({
				type: this.type,
				order: this.order
			})
		}
	},
	onLoad(option) {
		this.type = option.type
	},
	filters: {

	},
	computed: {
		...mapState(['newRecArr']),
	},
	watch: {

	},
	directives: {

	}
}
